@import "var";
*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	min-height: 100vh;
	max-width: 1320px;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	position: relative;
	font-family: $font-kumbh;
	@include breakpoint(xl) {
		font-size: larger;
	}
}
button {
	//reset to default
	font-family: inherit;
	outline: none;
	border: none;
	background: none;
	letter-spacing: inherit;
	font-size: inherit;
	text-align: inherit;
}
img {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	outline: 0;
}
